.prose {
    color: #374151;
    max-width:65ch
}

.prose [class ~=lead] {
    color: #4b5563;
    font-size: 1.25em;
    line-height: 1.6;
    margin-bottom: 1.2em;
    margin-top:1.2em
}

.prose a {
    color: #111827;
    font-weight: 500;
    text-decoration:underline
}

.prose strong {
    color: #111827;
    font-weight:600
}

.prose ol[type=a], .prose ol[type=a s] {
    --list-counter-style:lower-alpha
}

.prose ol[type=i], .prose ol[type=i s] {
    --list-counter-style: lower-roman
}

.prose ol[type="1"] {
    --list-counter-style:decimal
}

.prose ol > li {
    padding-left: 1.75em;
    position:relative
}

.prose ol > li:before {
    color: #6b7280;
    content: counter(list-item, var(--list-counter-style, decimal)) ".";
    font-weight: 400;
    left: 0;
    position:absolute
}

.prose ul > li {
    padding-left: 1.75em;
    position:relative
}

.prose ul > li:before {
    background-color: #d1d5db;
    border-radius: 50%;
    content: "";
    height: .375em;
    left: .25em;
    position: absolute;
    top: .6875em;
    width:.375em
}

.prose hr {
    border-color: #e5e7eb;
    border-top-width: 1px;
    margin-bottom: 3em;
    margin-top:3em
}

.prose blockquote {
    border-left-color: #e5e7eb;
    border-left-width: .25rem;
    color: #111827;
    font-style: italic;
    font-weight: 500;
    margin-bottom: 1.6em;
    margin-top: 1.6em;
    padding-left: 1em;
    quotes: "\201C""\201D""\2018""\2019"
}

.prose blockquote p:first-of-type:before {
    content:open-quote
}

.prose blockquote p:last-of-type:after {
    content:close-quote
}

.prose h1 {
    color: #111827;
    font-size: 2.25em;
    font-weight: 800;
    line-height: 1.1111111;
    margin-bottom: .8888889em;
    margin-top:0
}

.prose h2 {
    color: #111827;
    font-size: 1.5em;
    font-weight: 700;
    line-height: 1.3333333;
    margin-bottom: 1em;
    margin-top:2em
}

.prose h3 {
    font-size: 1.25em;
    line-height: 1.6;
    margin-bottom: .6em;
    margin-top:1.6em
}

.prose h3, .prose h4 {
    color: #111827;
    font-weight:600
}

.prose h4 {
    line-height: 1.5;
    margin-bottom: .5em;
    margin-top:1.5em
}

.prose figure figcaption {
    color: #6b7280;
    font-size: .875em;
    line-height: 1.4285714;
    margin-top:.8571429em
}

.prose code {
    color: #111827;
    font-size: .875em;
    font-weight:600
}

.prose code:after, .prose code:before {
    content: "`"
}

.prose a code {
    color:#111827
}

.prose pre {
    background-color: #1f2937;
    border-radius: .375rem;
    color: #e5e7eb;
    font-size: .875em;
    line-height: 1.7142857;
    margin-bottom: 1.7142857em;
    margin-top: 1.7142857em;
    overflow-x: auto;
    padding:.8571429em 1.1428571em
}

.prose pre code {
    background-color: transparent;
    border-radius: 0;
    border-width: 0;
    color: inherit;
    font-family: inherit;
    font-size: inherit;
    font-weight: 400;
    line-height: inherit;
    padding:0
}

.prose pre code:after, .prose pre code:before {
    content:none
}

.prose table {
    font-size: .875em;
    line-height: 1.7142857;
    margin-bottom: 2em;
    margin-top: 2em;
    table-layout: auto;
    text-align: left;
    width:100%
}

.prose thead {
    border-bottom-color: #d1d5db;
    border-bottom-width: 1px;
    color: #111827;
    font-weight:600
}

.prose thead th {
    padding-bottom: .5714286em;
    padding-left: .5714286em;
    padding-right: .5714286em;
    vertical-align:bottom
}

.prose tbody tr {
    border-bottom-color: #e5e7eb;
    border-bottom-width:1px
}

.prose tbody tr:last-child {
    border-bottom-width:0
}

.prose tbody td {
    padding: .5714286em;
    vertical-align:top
}

.prose {
    font-size: 1rem;
    line-height:1.75
}

.prose p {
    margin-bottom: 1.25em;
    margin-top:1.25em
}

.prose figure, .prose img, .prose video {
    margin-bottom: 2em;
    margin-top:2em
}

.prose figure > * {
    margin-bottom: 0;
    margin-top:0
}

.prose h2 code {
    font-size:.875em
}

.prose h3 code {
    font-size:.9em
}

.prose ol, .prose ul {
    margin-bottom: 1.25em;
    margin-top:1.25em
}

.prose li {
    margin-bottom: .5em;
    margin-top:.5em
}

.prose > ul > li p {
    margin-bottom: .75em;
    margin-top:.75em
}

.prose > ul > li > :first-child {
    margin-top:1.25em
}

.prose > ul > li > :last-child {
    margin-bottom:1.25em
}

.prose > ol > li > :first-child {
    margin-top:1.25em
}

.prose > ol > li > :last-child {
    margin-bottom:1.25em
}

.prose ol ol, .prose ol ul, .prose ul ol, .prose ul ul {
    margin-bottom: .75em;
    margin-top:.75em
}

.prose h2 + *, .prose h3 + *, .prose h4 + *, .prose hr + * {
    margin-top:0
}

.prose thead th:first-child {
    padding-left:0
}

.prose thead th:last-child {
    padding-right:0
}

.prose tbody td:first-child {
    padding-left:0
}

.prose tbody td:last-child {
    padding-right:0
}

.prose > :first-child {
    margin-top:0
}

.prose > :last-child {
    margin-bottom:0
}
